.profile {
  background-color: #f5f4fa;;
  padding: 70px;
}

.description {
  background-color: #fff;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);
  border-radius: 4px 4px 4px 4px;

}

.name {
  text-align: center;
  margin-bottom: 10px;
  color: #212121;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}

 .tag {
 text-align: center;
 margin-bottom: 16px;
 color: #757575;
 font-weight: bold;
 }

.location {
  text-align: center;
  padding-bottom: 20px;
 color: #757575;
 font-weight: bold;
 margin-bottom: 0;
}

.stats {
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin: auto;
  padding: 20px;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);
  border-radius: 0px 0px 4px 4px;

  
}

.avatar {
  display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
    padding: 50px;

}

.statList {
  color: #757575;
  padding: 50px;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);

 

}

.quantity {
  color: black;
  font-size: bold;
}